#myfooter {

    width: 100%;
    height: 20vh;
    position: absolute;
    top: 700px;
}

#three-icons {
    position: absolute;
    top: 50px;
    padding: 25px;

    left: 70px;
}

.bi-phone,
.bi-envelope-paper,
.bi-house-door {
    color: rgba(255, 255, 255, 0.637);
}

#house>h2,
#mail h2,
#phone h2 {
    padding: 10px;
    color: rgba(255, 255, 255, 0.637);
}

#house,
#mail,
#phone {
    display: flex;
}




.bi-facebook,
.bi-linkedin {
    color: rgba(255, 255, 255, 0.637);
    margin-right: 40px;
}

.two-icon {
    position: absolute;
    right: 200px;
    top: 60px;



}

#latest-part {
    padding: 15px;
    color: rgba(255, 255, 255, 0.637);
}






@media (max-width:800px) {

    #myfooter {

        display: none;
        position: absolute;
        top: 700px;
    }

    .two-icon {

        display: none;


    }

    #three-icons {
        margin-left: -50px;
        background-color: blueviolet;
        margin-top: 400px;
        /* display: none; */

    }

}


@media (min-width: 800px) and (max-width: 1024px) {

    .two-icon {

        display: none;


    }

    #three-icons {
        margin-left: -80px;
    }

    #myfooter {

        width: 100%;
        height: 30vh;
        position: relative;
        top: 700px;
        left: -50px;
    }

}